___CSS_LOADER_EXPORT___.push([module.id, `/* Navbar */
.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1.3rem;
    background-color: #1363df;
    color: white;
    position: sticky;
    top: 0;
    z-index: 1000;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  }
  
  /* Adjusting navbar logo size on smaller screens */
  .brand-logo {
    font-size: 1.5em;
    font-weight: bold;
  }
  
  /* For desktop and medium screens, center the nav tabs */
  .nav-tabs {
    display: flex;
    flex-direction: row;
    gap: 2rem;
    margin-left: auto;  /* Center the nav tabs */
    margin-right: auto; /* Center the nav tabs */
  }
  
  .nav-tab {
    color: white;
    text-decoration: none;
    position: relative;
    padding-block: 0.5rem;
  }
  
  .nav-tab:hover {
    color: #ddd;
  }
  
  .nav-tab.active::after {
    content: "";
    width: 100%;
    height: 2px;
    background-color: rgb(255, 254, 254);
    position: absolute;
    top: 100%;
    left: 0;
  }
  
  /* Notifications Styling */
  .notifications {
    position: relative;
    cursor: pointer;
  }
  
  .notifications i {
    color: #ddd;
  }
  
  .notifications::after {
    content: "0"; /* Example notification count */
    position: absolute;
    top: -10px;
    right: -10px;
    background-color: red;
    color: white;
    border-radius: 50%;
    padding: 2px 6px;
    font-size: 0.8em;
  }
  
  /* Hamburger Menu for Mobile */
  .hamburger-menu {
    display: none;
    font-size: 1.5rem;
    cursor: pointer;
  }
  
  .hamburger-menu i {
    color: white;
  }
  
  /* Drawer for Mobile */
  .drawer {
    position: fixed;
    left: -250px; /* Hidden initially */
    top: 0;
    width: 250px;
    height: 100%;
    background-color: #1363df;
    transition: left 0.3s ease;
    z-index: 1001;
  }
  
  .drawer.open {
    left: 0; /* Open drawer */
  }
  
  .drawer-content {
    padding: 1rem;
  }
  
  .nav-tabs {
    display: flex;
    /* flex-direction: column; */
    gap: 1rem;
  }
  
  .nav-tab {
    color: white;
    text-decoration: none;
    padding: 0.5rem;
  }
  
  .nav-tab:hover {
    color: #ddd;
  }
  
  .filter-list {
    width: 100%;
    padding-inline:1rem;
    padding-top: 2rem;
  }
  
  .apply-filters {
  
    background-color: #1363df;
    border-radius: 8px;
    color: white;
    border: none;
    cursor: pointer;
  }
  
  /* Mobile (320px) */
  @media (max-width: 320px) {
    .nav-tabs {
      gap: 1rem;
    }
    .brand-logo {
      font-size: 1.2em;
    }
    .nav-tabs {
      flex-direction: column;
    }
  }
  
  /* Tablet (768px) */
  @media (max-width: 768px) {
    .nav-tabs {
      gap: 1rem;
      flex-direction: column;
      padding-inline:1rem ;
    }
    .nav-tabs ::after, .nav-tab.active::after{
        content: "";
        width: 100%;
        height: 1px;
        background-color: rgb(5, 5, 5);
        position: absolute;
        top: 100%;
        left: 0;
      }

   
    .brand-logo {
      font-size: 1.4em;
    }

    .nav-tab.active{
        width: 100%;
        background-color: rgba(50, 176, 249, 0.223);
       
      }
    
  
    /* Show hamburger menu on mobile */
    .hamburger-menu {
      display: block;
    }
  
    /* Adjust filter and product list for mobile */
    .content-wrapper {
      flex-direction: column;
    }
    
    .product-list-wrapper {
      width: 100%;
    }
  }
  
  /* Desktop Small (1024px) */
  @media (max-width: 1024px) {
    .nav-tabs {
      gap: 1.5rem;
    }
  }
  
  /* Desktop Medium (1280px) */
  @media (max-width: 1280px) {
    .navbar {
      padding: 1.5rem;
    }
    .nav-tabs {
      gap: 1rem;
    }
    .nav-tabs {
      justify-content: center; /* Center the nav tabs */
    }
  }
  
  /* Desktop Large (1440px) */
  @media (max-width: 1440px) {
    .navbar {
      padding: 1.8rem;
    }
  }
  
  /* Desktop X-Large (1920px) */
  @media (max-width: 1920px) {
    .navbar {
      padding: 2rem;
    }
  }
  
/* Drawer (initially hidden) */
.drawer {
    display: none; /* Initially hidden */
    position: fixed;
    left: 0;
    top: 0;
    width: 30%; /* or any width you prefer */
    height: 100%;
    background-color:#fff ;
    color: rgb(0, 0, 0);
    padding: 1rem;
    border: 1px solid gray;
    transition: transform 0.3s ease;
    transform: translateX(-100%); /* Start from off-screen to the left */
  }
  
  /* When the drawer is open */
  .drawer.open {
    display: block;
    transform: translateX(0); /* Move into view */
  }
  
  /* Hamburger Menu Icon */
  .hamburger-menu {
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.5rem;
  }
  
  /* Drawer content inside */
  .drawer .nav-tabs {
    display: flex;
    flex-direction: column;
    
  }
  
  .drawer .nav-tab {
    padding: 1rem;
    text-decoration: none;
    color: rgb(0, 0, 0);
  }
  
  .drawer .nav-tab:hover {
    background-color: #0f4fa3;
  }
  
  /* Filter options */
  .drawer .filters-container {
    margin-top: 1.5rem;
  }
  
  .drawer .apply-filters {
    border: none;
    cursor: pointer;
  }
  
  /* Close button for the drawer */
  .close-drawer {
    display: flex;
    justify-content: space-between;
    cursor: pointer;
    font-size: 2rem;
    padding: 1rem;
    text-align: right;
  }

  .close-drawer  .brand-logo{
    font-size: 2rem;
  }
  
  .shopping-bag-icon{
    top: 38px;
    left: 1613px;
    width: 22px;
    height: 0px;
    /* UI Properties */
    border: 2px solid var(--white);
    border: 2px solid #FFFFFF;
    opacity: 1;
  }

  /* Responsive styles for mobile */
  @media (max-width: 768px) {
    .drawer {
      width: 80%; /* Adjust as needed */
    }
  }
