___CSS_LOADER_EXPORT___.push([module.id, `/* Banner Styles */
.banner {
    display: flex;
    position: relative;
    width: 100%;
    height: 15rem;
  }
  
  .banner-txt {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #1b252c;
    z-index: 1;
    position: relative;
    font-weight: bolder;
    color: #fff;
  }
  
  .banner-txt p {
    margin: 0;
    text-align: center;
    font-size: 2em;
  }
  
  .banner-txt::after {
    content: "";
    width: 5rem;
    height: 0.3rem;
    background-color: #1363df;
    position: absolute;
    left: 50%;
    top: 75%;
    transform: translateX(-50%);
  }
  
  .banner-img {
    flex: 2;
    overflow: hidden;
    position: relative;
    padding-block: 0.5rem;
  }
  
  .banner-img img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  
  @media (max-width: 768px) {
    .banner-img {
        padding: 0;
    }
    .banner-txt{
        width: 90%;
        height: 10rem;
        position: absolute;
        top:    70%;
        left: 50%;
        transform: translate(-50%,-10%);
    }
