* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Roboto', sans-serif;
}

/* Body Styles */
body {
  width: 100vw;
  height: 100vh;
}



/* Content Wrapper (Filter + Product List) */
.content-wrapper {
  display: flex;
  padding: 1rem;
  gap: 1rem;
  margin-top: 2rem;
  flex-wrap: wrap;
}

.filter-list {
  flex: 1;
  position: sticky;
  top: 20px;
  height: calc(100vh - 15rem);
  overflow-y: auto;
}

.filter-list h3 {
  margin-bottom: 1rem;
}

.filter-list label {
  display: block;
  margin-bottom: 0.5rem;
}

.filter-list input {
  margin-right: 0.5rem;
}



button {
  padding: 0.5rem 1rem;
  background-color: #1363df;
  color: white;
  border: none;
  cursor: pointer;
  margin-top: 2rem;
}

button:hover {
  background-color: #0f4fa3;
}

/* Mobile (320px) */
@media (max-width: 320px) {
  .product-list {
    grid-template-columns: 1fr; /* One column */
  }
  .content-wrapper {
    flex-direction: column;
  }
  .filter-list {
    width: 100%;
  }
}

/* Tablet (768px) */
@media (max-width: 768px) {
  .product-list {
    grid-template-columns: 1fr 1fr; /* Two columns */
  }
  .content-wrapper {
    flex-direction: column;
  }
  .filter-list {
    width: 100%;
  }

  .sort-count-box{
    margin-top: 3rem;
}
  
}

@media (max-width: 768px) {
    .sort-select {
      width: 100%;
      padding: 8px;
      font-size: 8px;
      border: 1px solid #ccc;
      border-radius: 5px;
      background-color: #fff;
      color: #333;
    }
  
    .sort-select:hover {
      border-color: #007bff;
    }
  
    .sort-select option {

      font-size: 10px;
    }
  
    .sort-select:focus {
      outline: none;
      border-color: #007bff;
    }
  
    .sort-select-label {
      font-size: 16px;
      margin-right: 10px;
      font-weight: bold;
      margin-bottom: 0.5rem;
    }
  
    .sort-count-box {
      margin-top: 1.5rem;
      width: 100%;
      justify-content: space-between;
      padding: 1rem 0;
    }
  }
  

/* Desktop Small (1024px) */
@media (max-width: 1024px) {
  .product-list {
    grid-template-columns: repeat(2, 1fr); /* Two columns */
  }
}

/* Desktop Medium (1280px) */
@media (max-width: 1280px) {
  .product-list {
    grid-template-columns: repeat(3, 1fr); /* Three columns */
  }
}

/* Desktop Large (1440px) */
@media (max-width: 1440px) {
  .product-list {
    grid-template-columns: repeat(4, 1fr); /* Four columns */
  }
}

/* Desktop X-Large (1920px) */
@media (max-width: 1920px) {
  .product-list {
    grid-template-columns: repeat(5, 1fr); /* Five columns */
  }
}


.sort-count-box{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 1rem;
}


.sort-select {
    width: 250px; /* Set the width of the dropdown */
    padding: 10px; /* Add padding to the select box */
    font-size: 16px; /* Set the font size */
    border: 1px solid #ccc; 
    border-radius: 5px; /* Rounded corners */
    background-color: #fff; 
    color: #333; 
    transition: all 0.3s ease; 
  }
  
  /* Hover effect for the select dropdown */
  .sort-select:hover {
    border-color: #007bff; /* Change border color on hover */
  }
  
  /* Style for the options inside the dropdown */
  .sort-select option {
    padding: 10px; /* Add padding to each option */
    font-size: 16px; /* Set font size for options */
  }
  
  /* Style for focus (when user clicks on it) */
  .sort-select:focus {
    outline: none; /* Remove default focus outline */
    border-color: #007bff; /* Change border color on focus */
  }
  
  /* Optional: Add a label for the dropdown */
  .sort-select-label {
    font-size: 18px; /* Font size for the label */
    margin-right: 10px; /* Space between label and dropdown */
    font-weight: bold; /* Bold text for the label */
  }
